.contact {
    width: 100%;
    padding-top: 84px;
    position: relative;
    background-color: $mainColor;
    @media (max-width: $md-3) {
        padding-top: 54px;
    }
    // .contact__car

    &__car {
        position: relative;
        top: 5px;
        @media (max-width: $md-4) {
            width: 100%;
        }
    }

    // .contact__vegitable

    &__vegitable {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        object-fit: cover;
    }

    // .contact__container

    &__container {
    }

    // .contact__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 10;
        position: relative;
    }

    // .contact__title

    &__title {
        margin-bottom: 38px;
        max-width: 600px;
        text-align: center;
        @media (max-width: $md-4) {
            max-width: 400px;
        }
    }

    // .contact__content

    &__content {
        width: 100%;
        display: flex;
        justify-content: center;
        @media (max-width: $md-3) {
            display: block;
        }
    }

    // .contact__form
    &__form._sending {
        &::after {
            visibility: visible;
        }
    }
    &__form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 857px;
        margin-bottom: 60px;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            visibility: hidden;
        }
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-bottom: 40px;
        }
    }

    // .contact__input
    &__input._error {
        box-shadow: 0 0 5px #ff4400;
    }
    &__input {
        outline: none;
        border: 1px solid #fff;
        font-style: 14px;
        
        padding: 0 30px;
        display: flex;
        align-items: center;
    }

    // .contact__button

    &__button {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        font-size: 18px;
        color :#fff;
        background-color: $orangeColor;
        outline: none;
        border: 1px solid $orangeColor;
        transition: .3s background-color, border linear 0s;
        &:hover {
            background-color: #ff4400;
            border: 1px solid #ff4400;
        }
        span {
            @include bg("contain");
            width: 32px;
            height: 9px;
            margin-left: 31px;
        }
    }

    &__item {
        width: 260px;
        height: 60px;
        border-radius: 57px;
        margin: 0 20px;
        @media (max-width: $md-3) {
            width: 100%;
            margin: 10px 0;
        }
    }
}
