.first-screen {
    width: 100%;
    min-height: 100vh;
    position: relative;

    &__background, &__vegetables, &__icons {
        position: absolute;
        left: 0;
        width: 100%;
        top: 0;
        height: 90%;
    }

    // .first-screen__background

    &__background {
        z-index: -50;
        object-fit: cover;
        @media (max-width: $md-3) {
            height: 100%;
        }
    }

    // .first-screen__vegetables

    &__vegetables {
        top: 150px;
        z-index: -48;
        object-fit: contain;
        height: auto;
    }

    // .first-screen__icons

    &__icons {
        z-index: -49;
        object-fit: contain;
    }

    // .first-screen__cobtainer

    &__cobtainer {
    }

    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        z-index: 5;
        position: relative;
        padding-top: 180px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .first-screen__title

    &__title {
        margin-bottom: 18px;
    }

    // .first-screen__text

    &__text {
        font-style: 18px;
        max-width: 621px;
        margin-bottom: 26px;
        text-align: center;
        @media (max-width: $md-3) {
           margin-bottom: 100px;
        }
    }

    // .first-screen__illustration

    &__illustration {
        width: 100%;

        display: flex;
        justify-content: flex-end;
        position: relative;
        @media (max-width: $md-3) {
            justify-content: center;
        }
    }

    // .first-screen__pizza

    &__pizza {
       position: relative;
       @media (max-width: $md-3) {
           width: 500px;
       }
       @media (max-width: $md-4) {
        width: 300px;
    }
    }

    // .first-screen__offer

    &__offer {
        position: absolute;
        z-index: 10;
        display: flex;
        top: 25%;
        right: -20px;
        @media (max-width: $md-1) {
            right: 0;
        }
        @media (max-width: $md-3) {
            top: 20%;
            
        }
        
    }

    // .first-screen__add

    &__add {
        cursor: pointer;
        @media (max-width: $md-4) {
            display: none;
            
        }
        position: relative;
        bottom: -50px;
        width: 45px;
        height: 45px;
        background-color: #fff;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 4px 8px rgba(205, 169, 41, 0.26);
        span {
            @include bg("contain");
            width: 15px;
            height: 15px
        }
    }

    // .first-screen__line

    &__line {
        @media (max-width: $md-4) {
            display: none;
            
        }
        position: relative;
    }

    // .first-screen__info

    &__info {
        text-align: right;
        max-width: 220px;
        position: relative;
        top: -20px;
        @media (max-width: $md-4) {
            top: -80px;
        }
        span {
            font-weight: 700;
            color: #F87848;
        }
    }
}

