.instagram {
    width: 100%;
    height: 260px;
    position: relative;
    margin-bottom: 127px;
    @media (max-width: $md-4) {
        margin-bottom: 70px;
    }
    // .instagram__photo

    &__photo {
        width: 100%;
        height: 100%;
        z-index: -50;
        object-fit: cover;
        top: 0;
        left: 0;
        position: absolute;
    }

    // .instagram__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 40px 0;
    }

    // .instagram__block

    &__block {
        width: 480px;
        height: 180px;
        background-color: $orangeColor;
        border-radius: 15px;
        padding: 20px;
        margin: 0 20px;
        @media (max-width: $md-6) {
            padding: 10px;
        }
    }

    // .instagram__wrapper

    &__wrapper {
        width: 100%;
        height: 100%;
        border: 1px solid rgba(255, 255, 255, 0.288);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }

    // .instagram__text

    &__text {
        font-style: 24px;
        color: #fff;
        text-align: center;
        a {
            font-weight: 700;
            color: #fff;
            text-decoration: none;
            margin-left: 10px;
            transition: .2s all linear 0s;
            padding-bottom: 1px;
            &:hover {
                border-bottom: 1px solid #fff;
            }
        }
    }
}
