.header {
    width: 100%; 
    height: 67px;
    margin-top: 25px;
    position: absolute; 
    top: 0;
    left: 0;
    z-index: 50;
    @media (max-width: $md-3) {
        margin-top: 10px;
        height: 50px;
        &::before {
            background-color: $mainColor;
        }
    }
    &::before {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: none;
        z-index: 2;
    }
    // .header__container

    &__container {
    }

    // .header__body

    &__body {
        
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
    }

    // .header__menu

    &__menu._active {
        left: 0;
    }
    &__menu {
        position: relative;
        z-index: 3;
        margin-bottom: 13px;
        overflow-y: auto;
        @media (max-width: $md-3) {
            z-index: 1;
            width: 100%;
            height: 100%;
            background-color: $mainColor;
            position: fixed;
            top: 0;
            transition: .4s all linear 0s;
            left: -100%;
            padding-top: 160px;
        }
    }

    // .header__items

    &__items {
        width: 330px;
        justify-content: flex-start;

        display: flex;
        align-items: center;
        padding: 0;
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: center;
            width: auto;
            justify-content: center;
        }
        li {
            list-style: none;
            margin-right: 40px;
            @media (max-width: $md-3) {
                margin-right: 0;
                margin-bottom: 40px;
            }
            &:last-child {
                margin-right: 0;
            }
            
        }
        a {
            color: #000;
            text-decoration: none;
            transition: .1s all linear 0s;
            cursor: pointer;
            &:hover {
                color: $blueColor;
            }
            @media (max-width: $md-3) {
                font-size: 25px;
            }
        }
    }

    // .header__logo

    &__logo {
        position: relative;
        z-index: 3;
        height: 100%;
        cursor: pointer;
        @media (max-width: $md-5 + 20px) {
            width: 60px;
            object-fit: contain;
        }
    }

    // .header__info

    &__info {
        width: 330px;
        justify-content: flex-end;

        position: relative;
        z-index: 3;
        display: flex;
        align-items: flex-end;
        margin-bottom: 13px;
        @media (max-width: $md-3) {
            width: auto;
            justify-content: center;
        }
        @media (max-width: $md-5) {
           flex-direction: column;
           align-items: center;
           margin-bottom: 3px;
        }
    }

    // .header__time

    &__time {
        position: relative; 
        &::after {
            position: absolute;
            top: 40%;
            left: -9px;
            content: "";
            display: block;
            width: 4px;
            height: 4px;
            background-color: #46CB72;
            border-radius: 50%;
        }
    }

    // .header__number

    &__number {
        margin-left: 10px;
        @media (max-width: $md-5) {
            margin: 0;
            margin-top: 5px;
        }
    }

     // .header__burger
     &__burger._active {
        &::before {
            top: 8px;
            transform: rotate(-45deg);
        }
        &::after {
            bottom: 8px;
            transform: rotate(45deg);
        }
        span {
            display: none;
        }
     }
    &__burger {
        display: none;
        @media (max-width: $md-3) {
            display: block;
            width: 30px;
            height: 18px;
            cursor: pointer;
            position: relative;
            z-index: 3;
            margin-bottom: 13px;
            &::before,
            &::after {
                position: absolute;
                content: "";
                display: block;
                width: 100%;
                height: 2px;
                background-color: #000;
                left: 0;
                transition: .3s all linear 0s;
            }
            &::before {
                top: 0;
            }
            &::after {
                bottom: 0;
            }
            span {
                position: absolute;
                top: 8px;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: #000;
            }
        }
    }
}
.number {
    display: flex;
  align-items: flex-end;
    // .number__ico

    &__ico {
        width: 45px;
        height: 45px;
        position: relative;
        bottom: -13px;
        @media (max-width: $md-5) {
            display: none;
        }
    }

    // .number__text

    &__text {
        margin-left: 5px;
        font-style: 18px;
        font-weight: 700;
        @media (max-width: $md-4) {
            margin-left: 0;
        }
    }
}

