.footer {
    width: 100%;
    height: 133px;
    background-color: #fff;
    @media (max-width: $md-4) {
        height: 120px;
        padding: 20px 0;
    }
    // .footer__container

    &__container {
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column-reverse;
        }
    }

    // .footer__copyright

    &__copyright {
        width: 300px;
        display: flex;
        justify-content: center;
        @media (max-width: $md-4) {
            width: 100%;
        }
    }

    // .footer__logo

    &__logo {
        width: 95px;
        height: 49px;
        object-fit: contain;
        cursor: pointer;
        @media (max-width: $md-4) {
            margin-bottom: 10px;
        }
    }

    // .footer__designed

    &__designed {
        width: 300px;
        display: flex;
        justify-content: center;
        @media (max-width: $md-4) {
            display: none;
        }
        span {
            font-weight: 700;
            margin-left: 4px;
        }
    }
}
