.dishes {
    width: 100%;
    position: relative; 
    padding-top: 120px;
    padding-bottom: 100px;
    @media (max-width: $md-4) {
        padding-bottom: 50px;
    }
    // .dishes__image1

    &__image1 {
        position: absolute;
        top: 5%;
        left: 0;
        z-index: -50;
    }

    // .dishes__image2

    &__image2 {
        position: absolute;
        top: 10%;
        right : 0;
        z-index: -50;
    }

    // .dishes__image3

    &__image3 {
        position: absolute;
        bottom: 0;
        left:0;
        z-index: -50;
    }
 
    // .dishes__basket

    &__basket {
        position: absolute;
        right: 50px;
        top: 0;
        @media (max-width: $md-3) {
            top: 15px;
        }
    }

    // .dishes__container

    &__container {
    }

    // .dishes__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .dishes__title

    &__title {
        margin-bottom: 50px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
         }
    }

    // .dishes__items

    &__items {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        margin-bottom: 50px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
         }
    }

    // .dishes__item

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $greyColor;
        transition: .3s all linear 0s;
        width: 135px;
        height: 55px;
        border-radius: 45px;
        margin: 0 15px;
        cursor: pointer;
        font-weight: 600;
        @media (max-width: $md-3) {
            margin: 10px 15px;
        }
        @media (max-width: $md-6) {
            width: 100%;
        }
        // .dishes__item_active

        &_active, &:hover {
            background-color: $blueColor;
            color: #fff;
        }
    }

    // .dishes__content
 
    &__content {
        margin-bottom: 66px;
    }

    // .dishes__more

    &__more {
        width: 60px;
        height: 60px;
        cursor: pointer;
        @include bg("contain");
    }
}
.dishes-basket {
    display: flex;
    align-items: center;
    
    // .dishes-basket__button

    &__button {
        width: 80px;
        height: 80px;
        background-color: #fff;
        border-radius: 50%;
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1);
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .3s all linear 0s;
    }

    // .dishes-basket__price
    &__price._active {
        display: flex;
    }
    &__price {
        min-width: 104px;
        height: 47px; 
        background-color: #3F3F3F;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        font-size: 18px;
        margin-right: 24px;
        color: #fff;
        font-weight: 600;
        border-radius: 5px;
        transition: .3s all linear 0s;
        display: none;
        &::before {
            position: absolute;
            top: 11px;
            right: -6px;
            content: "";
            display: block;
            background-color: #3F3F3F;
            z-index: -1;
           width: 25px;
           height: 25px;
           transform: rotate(45deg);
           border-radius: 4px;
        }
   }

    // .dishes-basket__ico

    &__ico {
        @include bg("contain");
        width: 35px;
        height: 35px;
    }

    // .dishes-basket__count

    &__count._unvisible {
        display: none;
    }
    &__count {
        position: absolute;
        bottom: 0;
        right: 0;
        background-color: $orangeColor;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        min-width: 25px;
        min-height: 25px;
        z-index: 2;
        padding: 2px;
        border-radius: 50%;
        
    }
}

.dishes-block {
    display: grid;
    grid-template-columns: repeat(4, 300px);
    @media (max-width: $md-1) {
        grid-template-columns: repeat(3, 300px);
    }
    @media (max-width: $md-3) {
        grid-template-columns: repeat(2, 300px);
    }
    @media (max-width: $md-4) {
        grid-template-columns: 300px;
    }
    // .dishes-block__item

    &__item {
        margin: 20px 20px;
        border: 2px solid $greyColor;
        border-radius: 15px;
        min-height: 400px;
        position: relative;
        padding: 36px 15px 0 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #fff;
        @media (max-width: $md-3) {
            margin: 20px 10px;
        }
       
        &::after {
            @media (max-width: $md-3) {
               left: 112px;
            }
            position: absolute;
            bottom: -25px;
            left: 102px;
            background-color: #fff;
            display: block;
            content: "";
            width: 50px;
            height: 50px;
            border-bottom: 2px solid $greyColor;
            border-right: 2px solid $greyColor;
            transform: rotate(45deg);
        }
    }

    // .dishes-block__message
 
    &__message {
        position: absolute;
        top: 20px;
        left: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $orangeColor;
        color: #fff;
        width: 60px;
        height: 25px;
        border-radius: 3px;
    }

    // .dishes-block__image

    &__image {
        width: 100%;
        height: 173px;
        object-fit: contain;
    }

    // .dishes-block__main

    &__main {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 8px;
    }

    // .dishes-block__name

    &__name {
        font-weight: 600;
    }

    // .dishes-block__size

    &__size {
        font-size: 12px;
    }

    // .dishes-block__description

    &__description {
        margin-bottom: 19px;
        font-size: 12px;
        text-align: left;
        height: 34px;   
    }

    // .dishes-block__price

    &__price {
        font-size: 24px;
        text-align: center;
        font-weight: 300;
        margin-bottom: 18px;
    }

    // .dishes-block__add

    &__add {
        position: relative;
        z-index: 3;
        top: 34px;
    }
}

.basket-add {
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #fff;
    border: 1px solid $greyColor;
    cursor: pointer;
    transition: .3s all linear 0s;
    &:hover {
        background-color: $orangeColor;
        .basket-add__off {
            display: none;
            opacity: 0;
        }
        .basket-add__on {
            display: block;
            opacity: 1;
        }
    }
    // .basket-add__ico

    &__ico {
        @include bg("contain");
        width: 15px;
        height: 15px;
    }

    // .basket-add__off

    &__off {
        display: block;
        opacity: 1;
    }

    // .basket-add__on

    &__on {
        display: none; 
        opacity: 0;
    }
}
