}
.dishes-basket-popup {
    overflow-y: auto;
    // .dishes-basket-popup__container

    &__container {
        width: 80%;
        @media (max-width: $md-4) {
           width: 90%;
          }
    }

    // .dishes-basket-popup__close

    &__close {
        outline: none;
        margin: 10px;
        @media (max-width: $md-4) {
           margin: 2px;
         }
    }

    // .dishes-basket-popup__content

    &__content {
        padding: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        @media (max-width: $md-4) {
            padding: 20px 5px;
         }
    }

    // .dishes-basket-popup__top

    &__top {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 50px;
        @media (max-width: $md-4) {
            margin-bottom: 30px;
         }
    }

    // .dishes-basket-popup__title

    &__title {
        display: flex;
        align-items: center
    }

    // .dishes-basket-popup__ico

    &__ico {
        @include bg("contain");
        width: 35px;
        height: 35px;
        margin-right: 5px;
        @media (max-width: $md-4) {
            width: 25px;
            height: 25px;
         }
    }

    // .dishes-basket-popup__text

    &__text {
    font-size: 40px;
    @media (max-width: $md-4) {
        font-size: 25px;
     }
    }

    // .dishes-basket-popup__clean

    &__clean {
        cursor: pointer;
        font-weight: 600;
        display: flex;
        align-items: center;
        text-align: center;
        @media (max-width: $md-6) {
            font-size: 14px;
         }
        span {
            @include bg("contain");
            width: 20px;
            height: 20px;
            margin-right: 3px;
            @media (max-width: $md-6) {
                display: none;
             }
        }
    }

    // .dishes-basket-popup__block

    &__block {
        width: 100%;
        margin-bottom: 40px;
        display: flex;
        flex-direction: column;
    }

    // .dishes-basket-popup__item

    &__item {
        width: 100%;
        padding: 10px 0;
        border-bottom: 1px solid #ccc;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
          flex-direction: column;
          justify-content: center;
          text-align: center;
        }
        @media (max-width: $md-6) {
            padding: 5px;
        }
    }

    // .dishes-basket-popup__image

    &__image {
        width: 150px;
        height: 150px;
        margin-right: 15px;
        @media (max-width: $md-3) {
            width: 100px;
            height: 100px;
            margin-right: 0;
        }
       
    }

    // .dishes-basket-popup__info

    &__info {
        display: flex;
        flex-direction: column;
    }

    // .dishes-basket-popup__caption

    &__caption {
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 5px;
        @media (max-width: $md-3) {
            font-size: 18px;
        }
        
    }

    // .dishes-basket-popup__description

    &__description {
        font-size: 15px;
        max-width: 320px;
        @media (max-width: $md-3) {
            max-width: 220px;
            font-size: 14px;
        }
    }

    // .dishes-basket-popup__description-text

    &__description-text {
    }

    // .dishes-basket-popup__items-count

    &__items-count {
        margin-right: 30px;
        @media (max-width: $md-3) {
            margin-right: 10px;
        }
        @media (max-width: $md-4) {
           margin-right: 0;
         }
    }

    // .dishes-basket-popup__price

    &__price {
        font-size: 24px;
        font-weight: 300;
        margin-right: 20px;
        text-align: center;
        @media (max-width: $md-3) {
            font-size: 20px;
            margin-right: 10px;
        }
    }

    // .dishes-basket-popup__delete

    &__delete {
        background-color: $orangeColor;
        border-radius: 50%;
        min-width: 40px;
        min-height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: .3s background-color, border linear 0s;
        font-size: 0;
        @media (max-width: $md-4) {
            background-color: transparent;
            color: $orangeColor;
            font-size: 14px;
            text-transform: uppercase;
            font-weight: 700;
            letter-spacing: 1px;
         }
        &:hover {
            background-color: #ff4400;
            @media (max-width: $md-4) {
               background-color: transparent;
             }
        }
        span {
            @include bg("contain");
            width: 20px;
            height: 20px;
            @media (max-width: $md-4) {
                display: none;
             }
        }
    }

    // .dishes-basket-popup__bottom

    &__bottom {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
         }
        @media (max-width: $md-4) {
            flex-direction: column;
            justify-content: center;
         }
    }

    // .dishes-basket-popup__count

    &__count {
        font-size: 18px;
        @media (max-width: $md-4) {
            margin-bottom: 10px;
         }
        span {
            font-weight: 600;
        }
    }

    // .dishes-basket-popup__total

    &__total {
        font-size: 18px;
        span {
            font-weight: 700;
            color: $orangeColor;
        }
    }

    // .dishes-basket-popup__button

    &__button {
        @include no-btn();
        width: 100%;
        background-color: $orangeColor;
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        text-align: center;
        height: 60px;
        border-radius: 59px;
        border: 1px solid $orangeColor;
        transition: .3s background-color, border linear 0s;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
            background-color: #ff4400;
            border: 1px solid #ff4400;
        }
    }
    
    &__empty._visible {
        display: block;
    }
    &__empty {
        font-weight: 600;
        font-size: 60px;
        color: $orangeColor;
        text-align: center;
        display: none;
        @media (max-width: $md-4) {
            font-size: 50px;
        }
    }
}
.count {
    display: flex;
    align-items: center;

    &__remove, &__add {
        border: 2px solid $orangeColor;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        margin: 0 12px;
        @media (max-width: $md-3) {
            margin: 10px;
            width: 30px;
            height: 30px;
        }
        span {
            @include bg("contain");
            width: 10px;
            height: 10px;
        }
    }

    // .count__remove

    &__remove {

    }

    // .count__number

	&__number {
        width: 21px;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
	}

    // .count__add

    &__add {
    }
}

